.c-btn{@apply .px-4 .py-2 .rounded .bg-grey-lighter;}.c-btn:hover{@apply .cursor-pointer;}.c-btn.block{@apply .w-full;}.c-btn.large{@apply .text-lg .py-3;}.c-btn.large{@apply .text-lg .py-3;}.c-btn.primary{@apply .bg-blue .text-white;}.c-btn.warning{@apply .bg-orange .text-white;}.c-btn.success{@apply .bg-green .text-white;}.c-btn.danger{@apply .bg-red .text-white;}.c-btn.info{@apply .bg-teal .text-white;}.c-btn[disabled]{@apply .opacity-50 .cursor-default;}.c-btn.outline{@apply .bg-transparent .border .border-solid;}.c-btn.outline.primary{@apply .border-blue .text-blue;}.c-btn.outline.warning{@apply .border-orange .text-orange;}.c-btn.outline.success{@apply .border-green .text-green;}.c-btn.outline.danger{@apply .border-red .text-red;}.c-btn.outline.info{@apply .border-teal .text-teal;}.c-btn.loading{@apply .relative;}.c-btn.loading.danger .loading-indicator>.animation,.c-btn.loading.info .loading-indicator>.animation,.c-btn.loading.primary .loading-indicator>.animation,.c-btn.loading.success .loading-indicator>.animation,.c-btn.loading.warning .loading-indicator>.animation{border-right-color:#fff;border-bottom-color:#fff}.c-btn.loading .loading-indicator{@apply .absolute;top:50%;left:50%;margin-top:-12px;margin-left:-12px}.c-btn.loading .c-btn-content{@apply .invisible;}.c-btn svg{@apply .mr-2;}.c-btn .c-btn-content{@apply .inline-block;}